.content {
  display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	background: linear-gradient(45deg, #F0C27B, #4B1248);
	border-radius: 1vmin;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	-ms-flex-direction: column;
	flex-direction: column;
	padding: 0.25vmax;
}

.content ul {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-flex: 1;
	-ms-flex: 1;
	flex: 1;
}
.content ul:first-of-type {
	margin-bottom: 0.25vmax;
}
.content ul li {
	padding: 0.3vmax;
	-webkit-box-flex: 1;
	-ms-flex: 1;
	flex: 1;
	cursor: pointer;
	font-weight: 600;
	font-size: 2vmin;
  text-align: center;
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}
.content ul li.active {
	background: #cbcaa5;
	-webkit-box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.4);
	box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.4);
	border-radius: 1.5vmin;
	cursor: auto;
}